ine-height: 1;\n    letter-spacing: normal;\n    text-transform: none;\n    display: inline-block;\n    white-space: nowrap;\n    word-wrap: normal;\n    direction: ltr;\n    -webkit-font-smoothing: antialiased; // Support for all WebKit browsers\n    -moz-osx-font-smoothing: grayscale; // Support for Firefox\n    text-rendering: optimizeLegibility; // Support for Safari and Chrome\n    font-feature-settings: 'liga'; // Support for IE\n  }\n}\n","@import 'core';\n\n@include material-symbols-font('Material Symbols Outlined');\n","@import 'core';\n\n@include material-symbols-font('Material Symbols Rounded');\n","@import 'core';\n\n@include material-symbols-font('Material Symbols Sharp');\n","$material-symbols-font-path: '~material-symbols/';\n@import 'material-symbols';\n\n@font-face {\n  font-family: 'futura';\n  src: url(../assets/Futura-Font-Family/futur.ttf);\n}\n\n* {\n  font-family: futura, sans-serif;\n}\n\nbody {\n  background: url('../assets/backgrounds/sunny.png') center center/cover\n    no-repeat fixed;\n}\n\n@mixin card {\n  border-radius: 15px;\n  background-color: rgba(255, 255, 255, 0.6);\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);\n  -webkit-backdrop-filter: blur(5px);\n  backdrop-filter: blur(5px);\n  padding: 0.5rem;\n}\n\n@mixin focused-input {\n  outline: none;\n  box-shadow: 0 0 0 1.5px rgba(0, 118, 214, 1) inset;\n}\n\n.search {\n  @include card;\n  position: relative;\n  margin: auto;\n  top: 4rem;\n  width: 50%;\n\n  &__form {\n    display: flex;\n    justify-content: center;\n  }\n\n  &__bar {\n    border: 1px solid grey;\n    border-radius: 10px 0 0 10px;\n    width: 70%;\n    padding-left: 0.8rem;\n\n    &:focus-visible {\n      @include focused-input();\n    }\n  }\n\n  &__button {\n    border: 1px solid grey;\n    border-radius: 0 10px 10px 0;\n    width: 10%;\n    text-align: center;\n\n    &:focus-visible {\n      @include focused-input();\n    }\n  }\n}\n\n.weather {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 80%;\n  height: 60%;\n  display: grid;\n  grid-template: repeat(3, minmax(50px, 0.5fr)) / repeat(4, minmax(100px, 1fr));\n  gap: 1rem;\n  grid-template-areas: 'temp temp temp feels-like' 'wind precip uv humidity' 'vis forecast forecast forecast';\n\n  > * {\n    @include card;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    text-align: center;\n\n    h5::before {\n      font-family: 'Material Symbols Outlined';\n      display: inline-block;\n      height: 24px;\n      width: 24px;\n      font-size: 0.9rem;\n    }\n\n    h5 {\n      position: absolute;\n      display: flex;\n      top: 0.5rem;\n      left: 1rem;\n      font-size: 0.9rem;\n    }\n\n    p {\n      margin: 0;\n      font-size: 1.5rem;\n    }\n\n    &:after {\n      content: '';\n      display: block;\n      height: 100%;\n    }\n  }\n\n  &__temp {\n    grid-area: temp;\n  }\n\n  &__feels-like {\n    grid-area: feels-like;\n\n    h5::before {\n      content: 'thermostat';\n    }\n  }\n\n  &__wind {\n    grid-area: wind;\n\n    h5::before {\n      content: 'air';\n    }\n  }\n\n  &__precip {\n    grid-area: precip;\n\n    h5::before {\n      content: 'water_drop';\n    }\n  }\n\n  &__uv {\n    grid-area: uv;\n\n    h5::before {\n      content: 'clear_day';\n    }\n  }\n\n  &__humidity {\n    grid-area: humidity;\n\n    h5::before {\n      content: 'airwave';\n    }\n  }\n\n  &__vis {\n    grid-area: vis;\n\n    h5::before {\n      content: 'visibility';\n    }\n  }\n\n  &__forecast {\n    grid-area: forecast;\n    display: grid;\n    grid-template-columns: repeat(3, minmax(100px, 1fr));\n\n    h5::before {\n      content: 'calendar_month';\n    }\n  }\n}\n\n.forecast {\n  > div {\n    display: flex;\n    justify-content: center;\n  }\n\n  > h6 {\n    margin: 2rem 0 0 0;\n    font-size: 0.8rem;\n  }\n\n  * h6,\n  * p {\n    font-size: 0.7rem;\n    padding: 0;\n    margin: 0;\n  }\n}\n\n@media (max-width: 700px) {\n  .search {\n    &__button{\n      width: 20%;\n    }\n  }\n\n  .weather {\n    grid-template: repeat(4, minmax(50px, 1fr)) / repeat(3, minmax(100px, 1fr));\n    grid-template-areas: 'temp temp temp' 'feels-like wind precip' 'uv humidity vis' 'forecast forecast forecast';\n    gap: 0.5rem;\n\n    > * {\n      h5::before {\n        font-size: 0.7rem;\n      }\n\n      h5 {\n        font-size: 0.7rem;\n        left: 0.5rem;\n      }\n\n      p {\n        font-size: 1rem;\n      }\n    }\n  }\n\n  .forecast {\n    > h6 {\n      font-size: 0.6rem;\n    }\n\n    * h6,\n    * p {\n      font-size: 0.5rem;\n    }\n  }\n}\n\n"],"sourceRoot":""}]);
